import React from 'react'
import { useDispatch } from 'react-redux'

import { loginAction } from "../../redux/actions/user"
import useInput from "../../hooks/useInput"
import Input from '../../ui/Input/Input'
import './auth.scss'

const Login = ({ setVisible }) => {
    const dispatch = useDispatch()

    const email = useInput("")
    const password = useInput("")

    const handleClick = () => {
        const user = {
            email: email.value,
            password: password.value
        }
        
        dispatch(loginAction(user))
        email.clear()
        password.clear()

        setVisible(false)
    }

    return (
        <div className="auth">
            <div className="auth__header">–ê–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—è</div>
            <Input placeholder="email..." value={email.value} onChange={email.onChange}/>
            <Input type="password" placeholder="–ü–∞—Ä–æ–ª—å..." value={password.value} onChange={password.onChange} />
            <button className="auth__btn" onClick={handleClick}>–í–æ–π—Ç–∏</button>
        </div>
    )
}

export default Login
