import React from 'react'
import Input from '../../ui/Input/Input'
import { useDispatch } from 'react-redux'

import { register } from '../../redux/actions/user'
import useInput from "../../hooks/useInput"

import './auth.scss'

const Register = ({ setVisible }) => {
    const dispatch = useDispatch()

    const name = useInput("")
    const email = useInput("")
    const password = useInput("")

    const handleClick = () => {
        const user = {
            name: name.value,
            email: email.value,
            password: password.value
        }
        dispatch(register(user))
        name.clear()
        email.clear()
        password.clear()

        setVisible(false)
    }

    return (
        <div className="auth">
            <div className="auth__header">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</div>
            <Input placeholder="–ò–º—è..." value={name.value} onChange={name.onChange} />
            <Input placeholder="Email..." value={email.value} onChange={email.onChange} />
            <Input type="password" placeholder="–ü–∞—Ä–æ–ª—å..." value={password.value} onChange={password.onChange} />
            <button className="auth__btn" onClick={handleClick}>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</button>
        </div>
    )
}

export default Register
